@import "../styles.scss";

.home {
  //background-image: url("../../public/home-page-room.png");
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
}

.home__room {
  grid-column-start: 1;
  grid-column-end: 11;
  grid-row-start: 1;
  grid-row-end: 11;
  width: 100%;
}

.home__books {
  grid-column-start: 5;
  grid-row-start: 8;
}

.home__sleepy {
  grid-column-start: 7;
  grid-row-start: 9;
}

.home__button {
  text-decoration: none;

  span {
    display: block;
    width: 100px;
    color: transparent;
    border: none;
    border-radius: 10px;
    height: auto;
    padding: 20px 10px;
    position: relative;
    margin: 20px;
    font-weight: bold;
    text-align: center;
  }

  &:hover {
    span {
      background-color: rgba(0, 0, 0, 0.8);
      color: #fff;

      &:after {
        content: " ";
        position: absolute;
        right: 35%;
        bottom: -15px;
        border-top: 15px solid black;
        border-right: 15px solid transparent;
        border-left: 15px solid transparent;
        border-bottom: none;
      }
    }
  }
}
