@import "../styles.scss";

.gallery {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 50px;

  img {
    width: 100%;
    max-width: 700px;
  }
}

.gallery__nav {
  position: fixed;
  bottom: 30px;
  right: 30px;
  text-align: center;
  button {
    background-color: transparent;
    border: 1px dotted $color__orange;
    padding: 10px;
    width: 200px;
  }
}
